<!--  -->
<template>
  <div class="">
    <!-- 左半侧 -->
    <el-row>
      <el-col :span="12">
        <div class="grid-content bg-purple">
          <el-form
            ref="elForm"
            :model="formData"
            :rules="rules"
            size="medium"
            label-width="100px"
            label-position="left"
          >
            <el-form-item label="试卷名称" prop="paparName">
              <el-input
                v-model="formData.paparName"
                placeholder="请输入试卷名称"
                :maxlength="100"
                show-word-limit
                clearable
                :style="{ width: '100%' }"
              ></el-input>
            </el-form-item>
            <el-form-item label="总分" prop="totalScore">
              <el-input-number
                v-model="formData.totalScore"
                placeholder="总分"
              ></el-input-number>
            </el-form-item>
            <el-form-item label="总时间" prop="totalTime">
              <el-input-number
                v-model="formData.totalTime"
                placeholder="总时间（单位分钟）"
              ></el-input-number>
            </el-form-item>
            <el-form-item label="开始时间" prop="beginTime">
              <el-date-picker
                type="datetime"
                v-model="formData.beginTime"
                format="yyyy-MM-dd HH:mm:ss"
                value-format="yyyy-MM-dd HH:mm:ss"
                :style="{ width: '100%' }"
                placeholder="请选择开始时间"
                clearable
              >
              </el-date-picker>
            </el-form-item>
            <el-form-item label="截止时间" prop="endTime">
              <el-date-picker
                type="datetime"
                v-model="formData.endTime"
                format="yyyy-MM-dd HH:mm:ss"
                value-format="yyyy-MM-dd HH:mm:ss"
                :style="{ width: '100%' }"
                placeholder="请选择截止时间"
                clearable
              >
              </el-date-picker>
            </el-form-item>
            <el-form-item label="添加题目" prop="endTime">
              <el-button type="success" @click="showSubject()"
                >添加题目
              </el-button>
              <span class="selectedScore"
                >已选分数:{{ selectedScore }} 设置总分:{{
                  formData.totalScore
                }}</span
              >
            </el-form-item>
            <el-form-item size="large">
              <el-button type="primary" @click="submitForm">保存</el-button>
              <el-button @click="resetForm">重置</el-button>
            </el-form-item>
          </el-form>
        </div>
      </el-col>

      <!-- 右半侧 -->
      <el-col :span="11">
        <div class="getPdf">
          <el-button type="success" @click="getPdf()">导出样卷</el-button>
        </div>

        <div class="grid-contentbg-purple-light" id="pdfDom">
          <div class="top">
            <div>
              <div>-</div>
              <div>-</div>
              <div>-</div>
              <div>-</div>
              <div>-</div>
              <div>-</div>
              <div>-</div>
            </div>
            <div>
              <div>-</div>
              <div>-</div>
              <div>-</div>
              <div>-</div>
              <div>-</div>
              <div>-</div>
              <div>-</div>
            </div>
          </div>

          <div class="showPaper">试卷预览</div>
          <span class="title">{{ formData.paparName }}</span>
          <div
            v-if="
              formData.totalScore != undefined &&
              formData.totalTime != undefined
            "
            class="title-bottom"
          >
            <span class="ss"
              >本试卷一共:{{selectedSubjects.length}}道题，总分为:{{ formData.totalScore }}分,总共用时{{
                formData.totalTime
              }}分钟</span
            >
            <span class="ss">出题人:{{ userInfo.memberName }}</span>
          </div>

          <!-- 题目信息 -->
          <div>
            <!-- 判断题列表区域 -->
            <div>
              <span v-if="judgmentSubjects.length > 0" class="subject-type"
                >一、判断题</span
              >
              <div
                v-for="(item, i) in judgmentSubjects"
                :key="i"
                :index="i"
                class="subject"
              >
                <div class="judgmentSubject-subjectName">
                  <div class="judgmentSubject-subjectName-left">
                    {{ i + 1 }} 、 {{ item.subjectName }}
                  </div>
                  <div class="judgmentSubject-subjectName-right">（ ）</div>
                </div>
                <div class="selection">
                  <img :src="item.subjectImage" class="subjectImage" />
                </div>
              </div>
            </div>

            <!-- 单选题区域 -->
            <div>
              <span v-if="singleSubjects.length > 0" class="subject-type"
                >二、单选题</span
              >
              <div
                v-for="(item, i) in singleSubjects"
                :key="i"
                :index="i"
                class="subject"
              >
                <span class="subjectName"
                  >{{ i + 1 }} 、 {{ item.subjectName }}</span
                >
                <div class="selection">
                  <div>
                    A,{{ item.aDescribtion }}<br />
                    B,{{ item.bDescribtion }}<br />
                    C,{{ item.cDescribtion }}<br />
                    D,{{ item.dDescribtion }}<br />
                  </div>
                  <img :src="item.subjectImage" class="subjectImage" />
                </div>
              </div>
            </div>

            <!-- 多选题区域 -->
            <div>
              <span v-if="noSingleSubjects.length > 0" class="subject-type"
                >三、多选题</span
              >
              <div
                v-for="(item, i) in noSingleSubjects"
                :key="i"
                :index="i"
                class="subject"
              >
                <span class="subjectName"
                  >{{ i + 1 }} 、 {{ item.subjectName }}</span
                >
                <div class="selection">
                  <div>
                    A,{{ item.aDescribtion }}<br />
                    B,{{ item.bDescribtion }}<br />
                    C,{{ item.cDescribtion }}<br />
                    D,{{ item.dDescribtion }}<br />
                  </div>
                  <img :src="item.subjectImage" class="subjectImage" />
                </div>
              </div>
            </div>

            <!-- 填空题列表 -->
            <div>
              <span v-if="completionSubjects.length > 0" class="subject-type"
                >四，填空题</span
              >
              <div
                v-for="(item, i) in completionSubjects"
                :key="i"
                :index="i"
                class="subject"
              >
                <div class="subjectName-completionSubject">
                  <div>{{ i + 1 }} 、 {{ item.subjectName }} : _____</div>
                </div>
                <div class="selection">
                  <img :src="item.subjectImage" class="subjectImage" />
                </div>
              </div>
            </div>

            <!-- 简答题列表 -->
            <div>
              <span v-if="shortAnswerSubjects.length > 0" class="subject-type">五、简答题</span>
              <div
                v-for="(item, i) in shortAnswerSubjects"
                :key="i"
                :index="i"
                class="subject"
              >
                <div class="subjectName-shortAnswerSubject">
                  {{ i + 1 }} 、 {{ item.subjectName }}
                </div>
                <div class="selection">
                  <img
                    :src="item.subjectImage"
                    class="subjectImage-shortAnswerSubject"
                  />
                </div>
              </div>
            </div>
          </div>
        </div>
      </el-col>
    </el-row>

    <!-- 显示题目弹窗 -->
    <el-dialog title="提示" :visible.sync="dialogVisible" width="80%">
      <el-form
        :inline="true"
        :model="dataForm"
        @keyup.enter.native="getDataList()"
      >
        <el-form-item>
          <el-input
            v-model="dataForm.key"
            placeholder="参数名"
            clearable
          ></el-input>
        </el-form-item>
        <el-form-item>
          <el-select
            v-model="subjectType"
            placeholder="题目类型"
            @change="changeType(subjectType)"
          >
            <el-option
              v-for="item in options"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            >
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button @click="getDataList()">查询</el-button>
        </el-form-item>
      </el-form>
      <el-table
        :data="dataList"
        border
        v-loading="dataListLoading"
        @selection-change="selectionChangeHandle"
        style="width: 100%"
      >
        <el-table-column
          prop="subjectName"
          header-align="center"
          align="center"
          label="题目描述"
        >
        </el-table-column>
        <el-table-column
          prop="aDescribtion"
          header-align="center"
          align="center"
          label="A选项描述"
        >
        </el-table-column>
        <el-table-column
          prop="bDescribtion"
          header-align="center"
          align="center"
          label="B选项描述"
        >
        </el-table-column>
        <el-table-column
          prop="cDescribtion"
          header-align="center"
          align="center"
          label="C选项描述"
        >
        </el-table-column>
        <el-table-column
          prop="dDescribtion"
          header-align="center"
          align="center"
          label="D选项描述"
        >
        </el-table-column>
        <el-table-column
          prop="subjectImage"
          header-align="center"
          align="center"
          label="题目图片"
        >
          <template slot-scope="scope">
            <img
              :src="scope.row.subjectImage"
              style="width: 50px; height: 80px"
            />
          </template>
        </el-table-column>

        <el-table-column
          prop="subjectType"
          label="题目类型"
          width="100"
          filter-placement="bottom-end"
        >
          <template slot-scope="scope">
            <el-tag disable-transitions>{{ scope.row.subjectType }}</el-tag>
          </template>
        </el-table-column>

        <el-table-column
          prop="subjectAnwser"
          header-align="center"
          align="center"
          label="题目答案"
        >
        </el-table-column>
        <el-table-column
          prop="catagoryId"
          header-align="center"
          align="center"
          label="所关联章节id"
        >
        </el-table-column>
        <el-table-column
          prop="score"
          header-align="center"
          align="center"
          label="题目分数"
        >
        </el-table-column>
        <el-table-column
          fixed="right"
          header-align="center"
          align="center"
          width="150"
          label="操作"
        >
          <template slot-scope="scope">
            <el-button
              type="text"
              size="small"
              @click="addSubject(scope.row)"
              :disabled="selectedSubjects.indexOf(scope.row.id) != -1"
              >添加
            </el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination
        @size-change="sizeChangeHandle"
        @current-change="currentChangeHandle"
        :current-page="pageIndex"
        :page-sizes="[10, 20, 50, 100]"
        :page-size="pageSize"
        :total="totalPage"
        layout="total, sizes, prev, pager, next, jumper"
      >
      </el-pagination>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="dialogVisible = false"
          >确 定</el-button
        >
      </span>
    </el-dialog>
  </div>
</template>

<script>
//这里可以导入其他文件（比如：组件，工具js，第三方插件js，json文件，图片文件等等）
//例如：import 《组件名称》 from '《组件路径》';

export default {
  //import引入的组件需要注入到对象中才能使用
  components: {},
  data() {
    //这里存放数据
    return {
      dataForm: {
        key: "",
      },
      dataListLoading: false,
      dataListSelections: [],
      pageIndex: 1,
      pageSize: 10,
      totalPage: 0,
      dataList: [],
      dialogVisible: false,
      formData: {
        paparName: undefined,
        totalScore: undefined,
        totalTime: undefined,
        beginTime: undefined,
        endTime: undefined,
      },
      rules: {
        paparName: [
          {
            required: true,
            message: "请输入试卷名称",
            trigger: "blur",
          },
        ],
        totalScore: [
          {
            required: true,
            message: "总分",
            trigger: "blur",
          },
        ],
        totalTime: [
          {
            required: true,
            message: "总时间（单位分钟）",
            trigger: "blur",
          },
        ],
        beginTime: [
          {
            required: true,
            message: "请选择开始时间",
            trigger: "change",
          },
        ],
        endTime: [
          {
            required: true,
            message: "请选择截止时间",
            trigger: "change",
          },
        ],
      },
      //单选题列表
      singleSubjects: [],
      //多选题列表
      noSingleSubjects: [],
      //填空题列表
      completionSubjects: [],
      //判断题列表
      judgmentSubjects: [],
      //简答题列表
      shortAnswerSubjects: [],
      //已添加题目id
      selectedSubjects: [],
      //已选分数
      selectedScore: 0,
      token: this.$store.state.user.token,
      //pdf文件名
      htmlTitle: '试卷预览',
      userInfo: this.$store.state.user.userInfo,
      options: [
        {
          value: 6,
          label: "全部",
        },
        {
          value: 0,
          label: "单选题",
        },
        {
          value: 1,
          label: "多选题",
        },
        {
          value: 3,
          label: "判断题",
        },
        {
          value: 2,
          label: "填空题",
        },
        {
          value: 4,
          label: "简答题",
        },
      ],
      subjectType: 6,
    };
  },
  //监听属性 类似于data概念
  computed: {},
  //监控data中的数据变化
  watch: {
    //监听分数是否超过当前规定的最大分数
    selectedScore: {
      handler(newValue, oldValue) {
        //如果当前分数已经超过了设置的最大分数  弹窗警告
        if (
          newValue >= this.formData.totalScore &&
          this.formData.totalScore != undefined
        ) {
          this.$message({
            message: `目前添加题目总分:${this.selectedScore},已经超过预设总分${this.formData.totalScore},请及时调整题目数量！`,
            type: "warning",
          });
        }
      },

      deep: true,
    },
  },
  //方法集合
  methods: {
    //发生改变的时候
    changeType(subjectType) {
      console.log(subjectType);
      this.getDataList();
    },

    //当前试卷添加题目
    addSubject(subject) {
      //加上题目的分数
      this.selectedScore += subject.score;
      this.selectedSubjects.push(subject.id);
      console.log(this.selectedSubjects);
      if (subject.subjectType === "单选题") {
        this.singleSubjects.push(subject);
      }
      if (subject.subjectType === "多选题") {
        this.noSingleSubjects.push(subject);
      }
      if (subject.subjectType === "填空题") {
        this.completionSubjects.push(subject);
      }
      if (subject.subjectType === "判断题") {
        this.judgmentSubjects.push(subject);
      }
      if (subject.subjectType === "简答题") {
        this.shortAnswerSubjects.push(subject);
      }

      console.log(this.singleSubjects);
    },
    // 多选
    selectionChangeHandle(val) {
      this.dataListSelections = val;
    },
    handleClose(done) {
      this.$confirm("确认关闭？")
        .then((_) => {
          done();
        })
        .catch((_) => {});
    },
    submitForm() {
      this.$refs["elForm"].validate((valid) => {
        if (!valid) return;
        //添加试卷
        this.$http
          .post(
            "/courseSystem/paper/addPaper",
            {
              paperName: this.formData.paparName,
              beginTime: this.formData.beginTime,
              endTime: this.formData.endTime,
              totalTime: this.formData.totalTime,
              totalScore: this.formData.totalScore,
              selectedSubjects: this.selectedSubjects,
            },
            {
              headers: {
                token: this.token,
              },
            }
          )
          .then(({ data }) => {
            this.$message({
              message: "添加试卷成功",
              type: "success",
              duration: 1500,
            });
            this.resetForm();
          });
      });
    },
    resetForm() {
      this.formData = {};
      this.singleSubjects = [];
      this.noSingleSubjects = [];
      this.completionSubjects = [];
      this.judgmentSubjects = [];
      this.shortAnswerSubjects = [];
      this.selectedSubjects = [];
      this.selectedScore = 0;
    },
    // 每页数
    sizeChangeHandle(val) {
      this.pageSize = val;
      this.pageIndex = 1;
      this.getDataList();
    },
    // 当前页
    currentChangeHandle(val) {
      this.pageIndex = val;
      this.getDataList();
    },
    //弹窗 显示题目列表
    showSubject() {
      this.dialogVisible = true;
      this.getDataList();
    },
    // 获取数据列表
    getDataList() {
      this.dataListLoading = true;
      this.$http
        .get("courseSystem/subject/list", {
          params: {
            page: this.pageIndex,
            pageSize: this.pageSize,
            key: this.dataForm.key,
            subjectType: this.subjectType,
          },
          headers: {
            token: this.token,
          },
        })
        .then(({ data }) => {
          if (data && data.code === 0) {
            this.dataList = data.page.list;
            this.dataList.map((item) => {
              if (item.subjectType == 1) {
                item.subjectType = "多选题";
              } else if (item.subjectType == 0) {
                item.subjectType = "单选题";
              } else if (item.subjectType == 2) {
                item.subjectType = "填空题";
              } else if (item.subjectType == 3) {
                item.subjectType = "判断题";
              } else if (item.subjectType == 4) {
                item.subjectType = "简答题";
              }
            });
            this.totalPage = data.page.totalCount;
          } else {
            this.dataList = [];
            this.totalPage = 0;
          }
          this.dataListLoading = false;
        });
    },
  },
  //生命周期 - 创建完成（可以访问当前this实例）
  created() {},
  //生命周期 - 挂载完成（可以访问DOM元素）
  mounted() {},
  beforeCreate() {}, //生命周期 - 创建之前
  beforeMount() {}, //生命周期 - 挂载之前
  beforeUpdate() {}, //生命周期 - 更新之前
  updated() {}, //生命周期 - 更新之后
  beforeDestroy() {}, //生命周期 - 销毁之前
  destroyed() {}, //生命周期 - 销毁完成
  activated() {}, //如果页面有keep-alive缓存功能，这个函数会触发
};
</script>
<style scoped>
.showPaper {
  display: flex;
  justify-content: center;
  font-weight: bold;
  margin-bottom: 20px;
}
.getPdf {
  display: flex;
  justify-content: center;
}
.title {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 30px;
  font-weight: bold;
}

.title-bottom {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 50px;
}

.subject {
  background-color: #eaeaea;
  margin-top: 20px;
  padding: 20px 20px;
  border-radius: 50px;
  margin-bottom: 20px;
}

.selection {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.subjectImage {
  width: 100px;
  height: 100px;
}

.grid-contentbg-purple-light {
  margin-left: 50px;
}

.judgmentSubject-subjectName {
  display: flex;
  justify-content: space-between;
  margin-bottom: 10px;
  font-weight: bold;
}

.subjectName-completionSubject {
  font-weight: bold;
  margin-bottom: 10px;
}

.subjectName-shortAnswerSubject {
  font-weight: bold;
  margin-bottom: 20px;
}

.subjectImage-shortAnswerSubject {
  width: 200px;
  height: 200px;
}

.selectedScore {
  margin-left: 10px;
  font-weight: bold;
  font-size: 15px;
  color: crimson;
}

.ss {
  font-weight: bold;
  margin-bottom: 20px;
}
.top {
  display: flex;
  justify-content: space-between;
}
.subjectName {
  font-weight: bold;
}
.subject-type {
  font-weight: bold;
  font-size: 20px;
}
</style>
